<template>
  <div>
    <!--  -->
    <el-breadcrumb separator-class="el-icon-arrow-right">
      <el-breadcrumb-item :to="{ path: '/home' }"></el-breadcrumb-item>
      <el-breadcrumb-item></el-breadcrumb-item>
      <el-breadcrumb-item></el-breadcrumb-item>
    </el-breadcrumb>
    <!--  -->
    <el-card>
      <!--  -->
      <el-table :data="rightsList" style="width: 100%" border stripe>
        <el-table-column type="index" width="50"></el-table-column>
        <el-table-column prop="authName" label=""></el-table-column>
        <el-table-column prop="path" label=""></el-table-column>
        <el-table-column prop="level" label="">
          <template slot-scope="scope">
            <el-tag type="success" v-if="scope.row.level==='0'"></el-tag>
            <el-tag type="danger" v-if="scope.row.level==='1'"></el-tag>
            <el-tag type="warning" v-if="scope.row.level==='2'"></el-tag>
          </template>
        </el-table-column>
      </el-table>
    </el-card>
  </div>
</template>

<script>
export default {
  data () {
    return {
      // 
      rightsList: []
    }
  },
  created () {
    this.getRightsList()
  },
  methods: {
    //  
    async getRightsList () {
      const { data: res } = await this.$axios.get('rights/list')
      if (res.meta.status !== 200) {
        // 
        this.$notify.error({
          title: '',
          message: res.meta.msg,
          duration: 2000
        })
      } else {
        // 
        this.rightsList = res.data // 
        this.$message.success(res.meta.msg)
      }
    }
  }
}
</script>

<style lang='less' scoped>
</style>
