<template>
  <div>
    <div>
      <!--  -->
      <el-breadcrumb separator-class="el-icon-arrow-right">
        <el-breadcrumb-item :to="{ path: '/home' }"></el-breadcrumb-item>
        <el-breadcrumb-item></el-breadcrumb-item>
        <el-breadcrumb-item></el-breadcrumb-item>
      </el-breadcrumb>
      <!--  -->
      <el-card>
        <!--  -->
        <el-button type="primary" style="margin-bottom:15px" @click="addUserBox"></el-button>
        <!--  -->
        <el-table :data="rolesList" border stripe row-key="id">
          <!--  -->
          <el-table-column type="expand">
            <!--  -->
            <template slot-scope="scope">
              <!--  -->
              <el-row
                :class="['row-bottom',index===0?'row-top':'','venter']"
                v-for="(item1,index) in scope.row.children"
                :key="item1.id"
              >
                <el-col :span="5">
                  <el-tag closable @close="removeTag(scope.row,item1.id)">{{item1.authName}}</el-tag>
                  <i class="el-icon-caret-right"></i>
                </el-col>
                <el-col :span="19">
                  <!--  -->
                  <el-row
                    :class="[index2===0?'':'row-top','venter']"
                    v-for="(item2,index2) in item1.children"
                    :key="item2.id"
                  >
                    <el-col :span="6">
                      <el-tag
                        type="success"
                        closable
                        @close="removeTag(scope.row,item2.id)"
                      >{{item2.authName}}</el-tag>
                      <i class="el-icon-caret-right"></i>
                    </el-col>
                    <el-col :span="18">
                      <!--  -->
                      <el-tag
                        type="warning"
                        closable
                        @close="removeTag(scope.row,item3.id)"
                        v-for="item3 in item2.children"
                        :key="item3.id"
                      >{{item3.authName}}</el-tag>
                    </el-col>
                  </el-row>
                </el-col>
              </el-row>
            </template>
          </el-table-column>
          <!--  -->
          <el-table-column type="index"></el-table-column>
          <el-table-column prop="roleName" label=""></el-table-column>
          <el-table-column prop="roleDesc" label=""></el-table-column>
          <el-table-column label="" width="300">
            <!--  -->
            <template slot-scope="scope">
              <el-button
                type="primary"
                icon="el-icon-edit"
                size="mini"
                @click="openEditBox(scope.row.id)"
              ></el-button>
              <el-button
                type="danger"
                icon="el-icon-delete"
                size="mini"
                @click="openDeleteBox(scope.row.id)"
              ></el-button>
              <el-button
                type="warning"
                icon="el-icon-setting"
                size="mini"
                @click="allotRights(scope.row)"
              ></el-button>
            </template>
          </el-table-column>
        </el-table>
      </el-card>
      <!--  -->
      <el-dialog title="" :visible.sync="dialogVisible" @close="removeTreekey" width="40%">
        <!--  -->
        <el-tree
          :data="rightsList"
          :props="treeProps"
          show-checkbox
          node-key="id"
          default-expand-all
          :default-checked-keys="defaultTree"
          ref="treeRef"
        ></el-tree>
        <span slot="footer" class="dialog-footer">
          <el-button @click="dialogVisible = false"> </el-button>
          <el-button type="primary" @click="AddAllotRights"> </el-button>
        </span>
      </el-dialog>
      <!--  -->
      <el-dialog title="" :visible.sync="dialogVisible2" width="40%" @close="resetAdduser">
        <el-form :model="addUserForm" :rules="addrules" ref="addruleForm" label-width="100px">
          <el-form-item label="" prop="roleName">
            <el-input v-model="addUserForm.roleName"></el-input>
          </el-form-item>
          <el-form-item label="" prop="roleDesc">
            <el-input v-model="addUserForm.roleDesc"></el-input>
          </el-form-item>
        </el-form>
        <span slot="footer" class="dialog-footer">
          <el-button @click="dialogVisible2 = false"> </el-button>
          <el-button type="primary" @click="submitAddUser"> </el-button>
        </span>
      </el-dialog>
      <!--  -->
      <el-dialog title="" :visible.sync="dialogVisible3" width="40%" @close="resetEdituser">
        <el-form :model="editUserForm" :rules="editrules" ref="editruleForm" label-width="100px">
          <el-form-item label="" prop="roleName">
            <el-input v-model="editUserForm.roleName"></el-input>
          </el-form-item>
          <el-form-item label="" prop="roleDesc">
            <el-input v-model="editUserForm.roleDesc"></el-input>
          </el-form-item>
        </el-form>
        <span slot="footer" class="dialog-footer">
          <el-button @click="dialogVisible3 = false"> </el-button>
          <el-button type="primary" @click="submitEditUser"> </el-button>
        </span>
      </el-dialog>
    </div>
  </div>
</template>
<script>
export default {
  data () {
    return {
      rolesList: [], // 
      dialogVisible: false, // /
      dialogVisible2: false, // /
      dialogVisible3: false,
      rightsList: [], // 
      treeProps: {
        // ,
        label: 'authName', // 
        children: 'children' // 
      },
      defaultTree: [], // ,
      keyId: '', // id,
      addUserForm: {
        // 
        roleName: '',
        roleDesc: ''
      },
      editUserForm: {
        // 
        roleName: '',
        roleDesc: ''
      },
      addrules: {
        roleName: [
          { required: true, message: '', trigger: 'blur' }
        ]
      },
      editrules: {
        roleName: [
          { required: true, message: '', trigger: 'blur' }
        ]
      }
    }
  },
  created () {
    this.getRolesList()
  },
  methods: {
    //  
    async getRolesList () {
      const { data: res } = await this.$axios.get('roles')
      if (res.meta.status !== 200) {
        // 
        return this.$notify.error({
          title: '',
          message: res.meta.msg,
          duration: 2000
        })
      } else {
        // 
        this.rolesList = res.data
        // this.$message.success(res.meta.msg);
      }
    },
    // ,,
    async removeTag (role, rightId) {
      const result = await this.$confirm(
        ', ?',
        '',
        {
          confirmButtonText: '',
          cancelButtonText: '',
          type: 'warning'
        }
      ).catch(err => err)
      if (result === 'confirm') {
        // 
        // 
        const { data: res } = await this.$axios.delete(
          `roles/${role.id}/rights/${rightId}`
        )
        if (res.meta.status !== 200) {
          // 
          this.$notify.error({
            // 
            title: '',
            message: res.meta.msg,
            duration: 2000
          })
        } else {
          // 
          this.$message.success(res.meta.msg)
          role.children = res.data // scope, roleslot-scope
        }
        this.$message.success(res.meta.msg)
      } else if (result === 'cancel') {
        // 
        this.$message.info('')
      }
    },
    // ,
    async allotRights (role) {
      this.keyId = role.id // id,,
      const { data: res } = await this.$axios.get('rights/tree')
      this.rightsList = res.data
      // ,id
      this.getTreekey(role, this.defaultTree)
      this.dialogVisible = true
    },
    // ,id
    getTreekey (node, array) {
      if (!node.children) {
        return array.push(node.id)
      }
      node.children.forEach(item => {
        this.getTreekey(item, array)
      })
    },
    // ,defaultTree
    removeTreekey () {
      this.defaultTree = []
    },
    // ,()
    async AddAllotRights () {
      // ,id
      const Keys = [
        ...this.$refs.treeRef.getCheckedKeys(), // id
        ...this.$refs.treeRef.getHalfCheckedKeys() // id
      ]
      const idStr = Keys.join(',') // ,
      // 
      const { data: res } = await this.$axios.post(
        `roles/${this.keyId}/rights`,
        { rids: idStr }
      )
      if (res.meta.status !== 200) {
        // 
        this.$notify.error({
          // 
          title: '',
          message: res.meta.msg,
          duration: 2000
        })
      } else {
        // 
        this.$message.success(res.meta.msg)
        this.getRolesList() // ,
      }
      this.dialogVisible = false // 
    },
    // 
    addUserBox () {
      this.dialogVisible2 = true
    },
    // 
    resetAdduser () {
      this.$refs.addruleForm.resetFields()
    },
    // ,
    submitAddUser () {
      this.$refs.addruleForm.validate(async valid => {
        if (!valid) {
          return this.$notify.error({
            title: '',
            message: ''
          })
        }
        // 
        const { data: res } = await this.$axios.post('roles', this.addUserForm)
        if (res.meta.status !== 201) {
          return this.$notify.error({
            title: '',
            message: res.meta.msg
          })
        }
        this.$message.success(res.meta.msg)
        this.getRolesList()
        this.dialogVisible2 = false
      })
    },
    // ,
    async openDeleteBox (Id) {
      const result = await this.$confirm(
        ', ?',
        '',
        {
          confirmButtonText: '',
          cancelButtonText: '',
          type: 'warning'
        }
      ).catch(err => err)
      if (result !== 'confirm') {
        this.$message.info('')
      } else {
        const { data: res } = await this.$axios.delete(`roles/${Id}`)
        if (res.meta.status !== 200) {
          return this.$message.error('')
        }
        this.$message.success('')
        this.getRolesList()
      }
    },
    // ,,id
    async openEditBox (Id) {
      this.dialogVisible3 = true
      const { data: res } = await this.$axios.get(`roles/${Id}`)
      if (res.meta.status !== 200) {
        return this.$message.error('')
      }
      this.editUserForm = res.data
    },
    // 
    resetEdituser () {
      this.$refs.editruleForm.resetFields()
    },
    // ,
    submitEditUser () {
      this.$refs.editruleForm.validate(async valid => {
        if (!valid) {
          return this.$notify.error({
            title: '',
            message: ''
          })
        }
        const { data: res } = await this.$axios.put(`roles/${this.editUserForm.roleId}`, {
          roleName: this.editUserForm.roleName,
          roleDesc: this.editUserForm.roleDesc
        })
        if (res.meta.status !== 200) {
          return this.$message.error('')
        }
        this.$message.success('')
        this.getRolesList()
        this.dialogVisible3 = false
      })
    }
  }
}
</script>

<style lang='less' scoped>
.el-tag {
  margin: 10px;
}
.row-top {
  border-top: 1px solid #eee;
}
.row-bottom {
  border-bottom: 1px solid #eee;
}
.venter {
  display: flex;
  align-items: center;
}
</style>
