<template>
  <div>
    <!--  -->
    <el-breadcrumb separator-class="el-icon-arrow-right">
      <el-breadcrumb-item :to="{ path: '/home' }"></el-breadcrumb-item>
      <el-breadcrumb-item></el-breadcrumb-item>
      <el-breadcrumb-item></el-breadcrumb-item>
    </el-breadcrumb>

    <el-card class="box-card">
      <el-row :gutter="20" class="row-bg">
        <!--  -->
        <el-col :span="8">
          <el-input placeholder="" v-model="queryInfo.query" clearable @clear="getUsersList">
            <el-button slot="append" icon="el-icon-search" @click="getUsersList"></el-button>
          </el-input>
        </el-col>
        <!--  -->
        <el-col :span="6">
          <el-button type="primary" @click="dialogVisible = true"></el-button>
        </el-col>
      </el-row>
      <!--  -->
      <el-row>
        <el-col>
          <el-table :data="tableData" border stripe style="width: 100%;margin-top:10px">
            <el-table-column type="index"></el-table-column>
            <el-table-column prop="username" label="" width="180"></el-table-column>
            <el-table-column prop="email" label=""></el-table-column>
            <el-table-column prop="mobile" label=""></el-table-column>
            <el-table-column prop="role_name" label=""></el-table-column>
            <el-table-column label="" width="100">
              <template slot-scope="scope">
                <!--slot-scope row -->
                <el-switch v-model="scope.row.mg_state" @change="stateChange(scope.row)"></el-switch>
              </template>
            </el-table-column>
            <el-table-column label="" width="220">
              <template slot-scope="scope">
                <!--slot-scope row -->
                <!--  -->
                <el-button type="primary" @click="amendUser(scope.row.id)" size="mini"></el-button>
                <!--  -->
                <el-button type="danger" size="mini" @click="deleteUser(scope.row.id)"></el-button>
                <!--  -->
                <el-tooltip effect="dark" content="" placement="top" :enterable="false">
                  <el-button type="warning" size="mini" @click="allocationUser(scope.row)"></el-button>
                </el-tooltip>
              </template>
            </el-table-column>
          </el-table>
        </el-col>
      </el-row>
      <!--  -->
      <el-pagination
        @size-change="handleSizeChange"
        @current-change="handleCurrentChange"
        :current-page="queryInfo.pagenum"
        :page-sizes="[1, 2, 5, 10]"
        :page-size="queryInfo.pagesize"
        layout="total, sizes, prev, pager, next, jumper"
        :total="total"
      ></el-pagination>
      <!--   -->
      <el-dialog title="" :visible.sync="dialogVisible" @close="resetFrom" width="40%">
        <!--   -->
        <el-form :model="UserForm" :rules="UserRules" ref="UserForm1" label-width="80px">
          <el-form-item label="" prop="username">
            <el-input v-model.trim="UserForm.username"></el-input>
          </el-form-item>
          <el-form-item label="" prop="password">
            <el-input v-model.trim="UserForm.password" type="password"></el-input>
          </el-form-item>
          <el-form-item label="" prop="email">
            <el-input v-model.trim="UserForm.email"></el-input>
          </el-form-item>
          <el-form-item label="" prop="mobile">
            <el-input v-model.trim="UserForm.mobile"></el-input>
          </el-form-item>
        </el-form>
        <!--  -->
        <span slot="footer" class="dialog-footer">
          <el-button @click="dialogVisible = false"> </el-button>
          <el-button type="primary" @click="addUser"> </el-button>
        </span>
      </el-dialog>
      <!--   -->
      <el-dialog title="" :visible.sync="dialogVisible2" width="40%">
        <!--   -->
        <el-form :model="amendUserForm" :rules="UserRules2" ref="UserForm2" label-width="80px">
          <el-form-item label="">
            <el-input v-model.trim="amendUserForm.username" :disabled="true"></el-input>
          </el-form-item>
          <el-form-item label="" prop="email">
            <el-input v-model.trim="amendUserForm.email"></el-input>
          </el-form-item>
          <el-form-item label="" prop="mobile">
            <el-input v-model.trim="amendUserForm.mobile"></el-input>
          </el-form-item>
        </el-form>
        <!--  -->
        <span slot="footer" class="dialog-footer">
          <el-button @click="dialogVisible2 = false"> </el-button>
          <el-button type="primary" @click="submitAmendUser"> </el-button>
        </span>
      </el-dialog>
      <!--   -->
      <el-dialog title="" :visible.sync="dialogVisible3" width="40%">
        <div>
          <p>{{this.allotUser.username}}</p>
          <p>{{this.allotUser.role_name}}</p>
          <el-select v-model="value" placeholder="">
            <el-option
              v-for="item in options"
              :key="item.id"
              :label="item.roleName"
              :value="item.id"
            ></el-option>
          </el-select>
        </div>
        <span slot="footer" class="dialog-footer">
          <el-button @click="dialogVisible3 = false"> </el-button>
          <el-button type="primary" @click="submitAllocationUser"> </el-button>
        </span>
      </el-dialog>
    </el-card>
  </div>
</template>
<script>
export default {
  created () {
    this.getUsersList()
  },
  data () {
    // 
    // 
    var checkEmail = (rule, value, callback) => {
      const regEmail = /^([a-zA-Z0-9_-])+@([a-zA-Z0-9_-])+(.[a-zA-Z0-9_-])+/
      // if (!value) {
      //   return callback(new Error(''))
      // }
      if (regEmail.test(value)) {
        callback()
      } else {
        callback(new Error(''))
      }
    }
    // 
    var checkMbolie = (rule, value, callback) => {
      const regMobile = /^1([38][0-9]|4[579]|5[0-3,5-9]|6[6]|7[0135678]|9[89])\d{8}$/
      // if (!value) {
      //   return callback(new Error(''))
      // }
      if (regMobile.test(value)) {
        callback()
      } else {
        callback(new Error('11'))
      }
    }
    return {
      queryInfo: { // 
        query: '', // ,,,
        pagenum: 1, // 
        pagesize: 10 // 
      },
      total: 0, // 
      tableData: [], // 
      dialogVisible: false, // 
      dialogVisible2: false, // 
      dialogVisible3: false, // 
      allotUser: {}, // 
      options: [], // 
      value: '', // 
      // ,
      UserForm: {
        username: '',
        password: '',
        email: '',
        mobile: ''
      },
      // ,
      UserRules: {
        username: [
          { required: true, message: '', trigger: 'blur' },
          { min: 3, max: 10, message: '3-10', trigger: 'blur' }
        ],
        password: [
          { required: true, message: '', trigger: 'blur' },
          { min: 6, message: '6', trigger: 'blur' }
        ],
        email: [
          { required: true, message: '', trigger: 'blur' },
          { validator: checkEmail } // 
        ],
        mobile: [
          { required: true, message: '', trigger: 'blur' },
          { validator: checkMbolie } // 
        ]
      },
      // ,
      amendUserForm: {},
      // ,
      UserRules2: {
        email: [
          { required: true, message: '', trigger: 'blur' },
          { validator: checkEmail } // 
        ],
        mobile: [
          { required: true, message: '', trigger: 'blur' },
          { validator: checkMbolie } // 
        ]
      }
    }
  },
  methods: {
    //  
    async getUsersList () {
      const { data: res } = await this.$axios.get('users', {
        params: this.queryInfo
      })
      if (res.meta.status !== 200) {
        this.$message.error('')
      } else {
        this.tableData = res.data.users // 
        this.total = res.data.total
      }
    },
    // pagesize ,
    handleSizeChange (newSize) {
      this.queryInfo.pagesize = newSize
      this.getUsersList() // 
    },
    // pagenum  ,
    handleCurrentChange (newPage) {
      this.queryInfo.pagenum = newPage
      this.getUsersList() // 
    },
    //  ,
    async stateChange (change) {
      const { data: res } = await this.$axios.put(
        `users/${change.id}/state/${change.mg_state}`
      )
      if (res.meta.status !== 200) {
        this.change.mg_state = !this.change.mg_state // ,
        this.$notify.error({
          title: '',
          message: res.meta.msg,
          duration: 2000
        })
      } else {
        this.$message.success(res.meta.msg)
      }
    },
    // ,
    resetFrom () {
      this.$refs.UserForm1.resetFields()
    },
    // ,
    amendUserresetFrom () {
      this.$refs.UserForm2.resetFields()
    },
    //  ,,
    addUser () {
      this.$refs.UserForm1.validate(async valid => {
        if (valid) {
          // ,
          const { data: res } = await this.$axios.post('users1', this.UserForm)
          if (res.meta.status !== 201) {
            this.$notify.error({
              title: '',
              message: res.meta.msg,
              duration: 2000
            })
          } else {
            this.$message.success(res.meta.msg)
            this.dialogVisible = false // ,
            this.getUsersList() // ,
          }
        } else {
          // ,
          return this.$notify.error({
            title: '',
            message: '',
            duration: 1000
          })
        }
      })
    },
    //  ,
    async amendUser (id) {
      this.dialogVisible2 = true
      const { data: res } = await this.$axios.get(`users/${id}`)
      if (res.meta.status !== 200) {
        this.$notify.error({
          title: '',
          message: res.meta.msg,
          duration: 2000
        })
      } else {
        this.amendUserForm = res.data // 
      }
    },
    //  ,,
    submitAmendUser () {
      this.dialogVisible2 = false
      this.$refs.UserForm2.validate(async valid => {
        if (valid) {
          // ,
          const { data: res } = await this.$axios.put(
            `users/${this.amendUserForm.id}`,
            {
              email: this.amendUserForm.email,
              mobile: this.amendUserForm.mobile
            }
          )
          if (res.meta.status !== 200) {
            // 
            this.$notify.error({
              // 
              title: '',
              message: res.meta.msg,
              duration: 2000
            })
            this.dialogVisible2 = true // 
          } else {
            // 
            this.dialogVisible2 = false // 
            this.getUsersList() //  ,
            this.$message.success(res.meta.msg)
          }
        } else {
          // , 
          this.dialogVisible2 = true // 
          return this.$notify.error({
            title: '',
            message: '',
            duration: 1000
          })
        }
      })
    },
    //  
    async deleteUser (id) {
      // 
      const result = await this.$confirm(
        ', ?',
        '',
        {
          confirmButtonText: '', // ,result confirm 
          cancelButtonText: '', // ,result cancel 
          type: 'warning'
        }
      ).catch(err => err)
      if (result === 'confirm') {
        // 
        // 
        const { data: res } = await this.$axios.delete(`users/${id}`)
        if (res.meta.status !== 200) {
          // 
          this.$notify.error({
            // 
            title: '',
            message: res.meta.msg,
            duration: 2000
          })
        } else {
          // 
          this.$message.success(res.meta.msg) // 
          this.getUsersList() // 
        }
      } else {
        // 
        this.$message.info('')
      }
    },
    //   /
    async allocationUser (user) {
      this.dialogVisible3 = true
      this.allotUser = user
      // 
      const { data: res } = await this.$axios.get('roles')
      this.options = res.data // 
    },
    //  
    async submitAllocationUser () {
      if (!this.value) {
        this.$notify.error({
          title: '',
          message: '',
          duration: 2000
        })
      } else {
        //  
        const { data: res } = await this.$axios.put(
          `users/${this.allotUser.id}/role`,
          { rid: this.value }
        )
        if (res.meta.status !== 200) {
          this.$notify.error({
            title: '',
            message: res.meta.msg,
            duration: 2000
          })
        } else {
          this.$message.success(res.meta.msg)
          this.getUsersList()
          this.dialogVisible3 = false
        }
      }
    }
  }
}
</script>

<style lang='less' scoped>
</style>
